import { fetchCurrencyHistory } from '@api/coin/fetchCurrencyHistory';
import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';

export interface CurrencyHistoryState {
  data: Array<{ time_open: string; rate_close: number }>;
  isLoading: boolean;
  error: string | null;
  selectedCurrency: string | null;
  daysAmount: string;
}

const initialState: CurrencyHistoryState = {
  data: [],
  isLoading: false,
  error: null,
  selectedCurrency: null,
  daysAmount: '',
};

export const fetchCurrencyHistoryThunk = createAsyncThunk(
  'currencyHistory/fetchCurrencyHistory',
  async ({ currency, days }: { currency: string; days: number }) => {
    return await fetchCurrencyHistory(currency, days);
  },
);

const currencyHistorySlice = createSlice({
  name: 'currencyHistory',
  initialState,
  reducers: {
    setSelectedCurrency(state, action: PayloadAction<string>) {
      state.selectedCurrency = action.payload;
    },
    setDaysAmount(state, action: PayloadAction<string>) {
      state.daysAmount = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchCurrencyHistoryThunk.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(
        fetchCurrencyHistoryThunk.fulfilled,
        (
          state,
          action: PayloadAction<
            Array<{ time_open: string; rate_close: number }>
          >,
        ) => {
          state.isLoading = false;
          state.data = action.payload;
        },
      )
      .addCase(fetchCurrencyHistoryThunk.rejected, (state, action) => {
        state.isLoading = false;
        state.error =
          action.error.message || 'Failed to fetch currency history';
      });
  },
});

export const { setSelectedCurrency, setDaysAmount } =
  currencyHistorySlice.actions;
export default currencyHistorySlice.reducer;
