import { API_CONFIG } from '@constants/config';
import axios from 'axios';

interface CurrencyData {
  time_open: string;
  rate_close: number;
}

export const fetchCurrencyHistory = async (currency: string, days: number) => {
  const { COIN_API_HISTORY_URL, COIN_API_KEY } = API_CONFIG;

  try {
    const endDate = new Date();
    const startDate = new Date(endDate);
    startDate.setDate(endDate.getDate() - days);

    const startDateStr = startDate.toISOString();
    const endDateStr = endDate.toISOString();

    const response = await axios.get(
      `${COIN_API_HISTORY_URL}/${currency}/USD/history`,
      {
        headers: { 'X-CoinAPI-Key': COIN_API_KEY },
        params: {
          period_id: '1DAY',
          time_start: startDateStr,
          time_end: endDateStr,
        },
      },
    );

    console.log(response.data);

    return response.data.map((data: CurrencyData) => ({
      time_open: data.time_open,
      rate_close: data.rate_close,
    }));
  } catch (error) {
    console.error(error);
    throw new Error('Failed to fetch data history');
  }
};
